@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@700;800&display=swap');

:root {

  --main-red: #c92a2a;
  --light-red: #cc6666;
  --main-color: #087f5b;
  --main-bg-dark-color: #000000;
  --default-margin: 10px;

  font-size: 62.5%;
}

body {
  background-color: var(--main-bg-dark-color);
}


html, body, #root {
  height: 100%;
  width: 100%;
}




.container {
  width: 100%;
  height: 100%;
  color: white;
}

.centered-flex {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}



#main-box {

  display: flex;
  align-items: center;
  align-content: center;
  padding: 5px;
  height: 150px;
}

.me {
  height: 100%;
  border-radius: 50%;
  margin: var(--default-margin);
}


.icon-container {
  display: flex;
  flex-direction: row;;
  align-content: center;
  justify-content: center;
  align-items: stretch;
  height: auto;
  padding: 0px 15px;
}

.icon-container a {

  padding: 5px 5px;
  color: white;
}


.intro {
  font-size: clamp(1rem, 10vw, 10rem);
}


.title {
  padding: var(--default-margin);
  font-size: clamp(1rem, 10vw, 10rem);
  font-weight: 800;
  height: fit-content;
  width: fit-content;
  font-family: 'Lato', sans-serif;
  color: white;
  mix-blend-mode: difference;

  margin: var(--default-margin);
}

.highlight {

}


.react-p5 {
  width: clamp(1rem, 500px, 30rem);
  border-radius: 5px;
  border: 5px black;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -1;
  background-color: var(--main-bg-dark-color);
}

.react-p5 canvas {
  width: 100% !important;
  height: 100% !important;
}


.shadow {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.social-icon {
  transition-duration: 0.4s;
  height: auto;
}

.social-icon:hover {
  transform: translate(-2px, -2px);
}

